export const initMap = (element) => {
  const center = { lat: -25.344, lng: 131.036 };
  const map = new google.maps.Map(element, { zoom: 4, center });
  const marker = addMapMarker(center, map);
};

export const addMapMarker = (position, map) => {
  return new google.maps.Marker({ position, map });
}